import React, { useState, useEffect } from 'react';
import styled, { keyframes, css } from 'styled-components';
import { ViewDefault, TextDefualt } from '@views/atoms/Custom';

type itemType = {
	component: '' | React.ReactNode;
	path: string;
	fileName: string;
	title: string;
	author: string;
	date: string;
	categories: string;
	tags: string;
	toc: string;
};

type BlogCard = {
	item: itemType;
	index: number;
};
function BlogCard({ item, index }: BlogCard) {
	const [show, setShow] = useState(false);

	useEffect(() => {
		setTimeout(() => {
			setShow(true);
		}, index * 200);
	}, []);

	const onClickBlogItem = (fileName: string) => {
		if (fileName) {
			console.log(fileName);
		}
	};
	return (
		<View.Container show={show} onClick={() => onClickBlogItem(item.fileName)}>
			<Text.Title>{item.title}</Text.Title>
			<Text.SubTitle>{item.date + '  |  ' + item.categories}</Text.SubTitle>
		</View.Container>
	);
}

export default BlogCard;

const View = {
	Container: styled(ViewDefault)`
		display: none;
		width: 100%;
		height: 200px;
		justify-content: center;
		border-bottom: 1px solid black;
		${(props: FadeInType) => {
			if (props.show) {
				return css`
					animation: ${FadeIn} 2s 0s 1 linear;
					display: flex;
				`;
			}
		}}
	`,
};

const Text = {
	Title: styled(TextDefualt)`
		font-size: 30px;
		color: #333333;
		cursor: pointer;
		font-weight: bold;
	`,
	SubTitle: styled(TextDefualt)`
		font-size: 20px;
		color: #eceef0;
	`,
};
type FadeInType = {
	show: boolean;
};
const FadeIn = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`;
